import React from 'react'
import { Helmet } from 'react-helmet'

const Meta = ({title, description, keywords}) => {
  return (
    <Helmet>
      <title>{title}</title>
      <meta name='description' content={description}/>
      <meta name='keywords' content={keywords}/>
    </Helmet>
  )
}

Meta.defaultProps = {
    title: 'Welcome to HollyShop!',
    description: 'We sell the cutest products for the best prices!',
    keywords:'squishmallows, cute, aesthetic'
}

export default Meta
